@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.container {
	align-items: center;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-bottom: 15px; }

.total {
	font-size: 18px;
	white-space: nowrap;
	margin-right: 15px;
	margin-bottom: 10px;
	@include r(767) {
		font-size: 16px; } }

.filters {
	display: flex;
	flex-wrap: wrap;
	> * {
		margin-bottom: 10px;
		&:not(:last-child) {
			margin-right: 10px; } } }
