@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.container {
	padding-top: 23px;
	@include r(575) {
		padding-top: 15px; }
	& :global(.column__row) {
		&:not(:first-child) {
			margin-top: 10px;
			margin-bottom: 10px; } } }

.control-wrap {
	display: flex;
	align-items: flex-end;
	position: relative;
	margin-top: 5px;
	 :global(.fico) {
		font-size: 20px;
		color: $grey;
		position: absolute;
		top: 50%;
		margin-top: -12px;
		left: 5px;
		z-index: 1;
		& + .input {
			margin-left: 20px; }
		& + .select {
			 :global(.rs__control) {
				padding-left: 12px; }
			 :global(.rs__menu) {
				z-index: 2; } } }
	.select {
		 :global(.rs__control) {
			height: 31px; }
		 :global(.rs__dropdown-indicator) {
			padding: 5px 8px; } }
	&--input {
		padding: 4px 0 5px;
		 :global(.fico) {
			cursor: pointer; } }
	&--select {
		 :global(.fico) {
			pointer-events: none; } } }

.input {
	font-family: inherit;
	font-size: 14px;
	border: none;
	border-bottom: 1px solid currentColor;
	font-weight: 500;
	text-overflow: ellipsis;
	padding: 0 2px;
	position: relative;
	&:focus {
		outline: 0; } }

.checkbox {
	&__label {
		font-size: 14px; } }
