@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.btns {
	& > :not(:first-child) {
		margin-left: 15px; }
	.todo {
		@include rm(768) {
			max-width: 380px; }
		@include r(767) {
			margin-left: 0;
			width: 100%;
			order: 1;
			margin-bottom: 15px; } }
	button {
		@include r(767) {
			order: 2;
			&:first-of-type {
				margin-left: 15px; } } } }
