@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.list {
	padding: 0;
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	margin: 70px 0;
	li {
		margin-bottom: 10px;
		&:not(:last-child) {
			margin-right: 35px; } }
	@include r(767) {
		margin: 35px 0; } }

.link {
	font-family: inherit;
	color: inherit;
	font-size: 20px;
	line-height: 30px;
	font-weight: bold;
	text-decoration: underline;
	white-space: nowrap;
	background: transparent;
	padding: 0;
	border: 0;
	cursor: pointer;
	&:focus {
		outline: 0; }
	&.active {
		text-decoration: none; }
	@include r(767) {
		font-size: 18px; } }
