@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.param {
	position: relative;
	font-size: 12px;
	font-weight: bold;
	margin-top: 5px;
	&[data-tip] {
		cursor: help; }
	&:not(:last-child) {
		margin-right: 20px; }
	& :global(.fico) {
		color: $grey;
		font-size: 20px;
		margin-right: 15px;
		vertical-align: middle; } }

.vp__group {
	align-items: flex-start;
	display: flex;
	@include r(767) {
		flex-wrap: wrap; } }
