@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.sign {
	position: relative;

	&__controls {
		padding: 15px 15px 0;
		margin: 15px -15px 0;
		border-top: 1px solid $lightGrey3; }

	&__field {
		border-radius: 3px;
		border: 1px solid $lightGrey3;
		background-color: $lightGrey5;
		position: relative;
		width: 766px;
		height: 460px;
		max-width: 100%;
		max-height: 100%;
		overflow: hidden;
		margin: 0 auto;
		canvas {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%; } }

	&__preview {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		max-width: 90%;
		max-height: 90%;
		user-select: none; } }
