@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.container {
	@include r(767) {
		width: 230px; }
	@include r(575) {
		width: 100%; } }

.item {
	flex: 1 0 50%;
	&:not(:first-child) {
		margin-top: 10px; } }

.control-wrap {
	display: flex;
	align-items: center;
	cursor: pointer;
	margin-top: 5px;
	 :global(.fico) {
		font-size: 20px;
		margin-right: 5px;
		color: $grey; } }

.date-val {
	font-size: 14px;
	font-weight: 500; }

.time {
	margin-top: 3px; }
