@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.wrapper {
	flex-shrink: 0;
	width: 100%;
	max-width: 548px;
	padding: 15px;
	@include rh(435) {
		align-self: flex-start; } }

.info {
	width: 100%;
	padding: 18px 28px;
	font-size: 16px;
	line-height: 25px;
	color: $grey;
	border: 1px solid $green;
	border-radius: 3px;
	margin-bottom: 10px; }

.inner {
	width: 100%;
	padding: 40px 15px 50px;
	border-radius: 4px;
	border: 1px solid $lightGrey3;
	text-align: center;
	box-shadow: 0px 20px 80px rgba(71, 91, 131, 0.1);
	.title {
		color: $grey;
		font-size: 30px;
		font-weight: bold; } }

.form {
	width: 100%;
	max-width: 250px;
	.form__buttons {
		margin-top: 30px; } }

.wrapper {
	.form {
		margin: 40px auto 0; } }

.input {
	display: block;
	font-family: inherit;
	background: transparent;
	border: 0;
	color: $grey;
	width: 100%;
	padding: 15px;
	&:focus {
		outline: 0;
		color: black; }
	&__wrap {
		position: relative;
		width: 100%;
		border: 1px solid $lightGrey5;
		background-color: $lightGrey5;
		border-radius: 3px;
		&:focus-within {
			background-color: transparent;
			border-color: $lightGrey3; }
		&.error {
			background-color: transparent;
			border-color: $red; } }
	&__error {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		overflow: hidden;
		text-overflow: ellipsis;
		transform: translateY(-50%);
		text-align: left;
		padding: 0 10px;
		pointer-events: none;
		span {
			display: inline-block;
			font-size: 12px;
			background-color: white;
			color: $red;
			padding: 0 5px; } } }
