@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";


.container {
	display: inline-block; }

.label {
	position: relative;
	display: inline-block;
	font-size: inherit;
	padding-left: 25px;
	cursor: pointer;
	user-select: none;
	&::before,
	&::after {
		content: "";
		position: absolute;
		top: 7px; }
	&::before {
		left: 0;
		transform: translateY(-50%);
		width: 14px;
		height: 14px;
		background-color: rgba(255, 255, 255, 0);
		border: 1px solid $lightGrey3;
		border-radius: 3px; }
	&::after {
		left: 3px;
		width: 13px;
		height: 9px;
		border-left: 2px solid $black;
		border-bottom: 2px solid $black;
		transform: translateY(-97%) rotate(-45deg);
		display: none; } }
 :global(.is-keyless) {
	.label {
		&::after {
			border-left-color: $keyless;
			border-bottom-color: $keyless; } } }
 :global(.is-standard) {
	.label {
		&::after {
			border-left-color: $standard;
			border-bottom-color: $standard; } } }

.checkbox {
	position: absolute;
	z-index: -1;
	opacity: 0;
	&:disabled {
		& + .label {
			opacity: 0.8;
			cursor: not-allowed;
			&::after {
				border-color: $grey; } } }
	&:checked {
		& + .label {
			&::after {
				display: block; } } } }
