@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.bc {
	 :global(.column--last) {
		@include r(575) {
			align-items: flex-start;
			text-align: left;
			margin-left: unset;
			margin-top: 15px; } } }


.links-list {
	margin-top: 15px; }

.payment {
	width: 183px;
	 :global(div.rs) {
		&__control {
			max-width: 100%;
			border-radius: unset;
			border: none;
			position: relative; }
		&__indicators {
			position: absolute;
			right: 0; }
		&__value-container {
			padding-right: 36px;
			width: 100%;
			height: 100%;
			&:global(:not(.rs__value-container--has-value)) {
				border-radius: 5px;
				border: 1px solid $lightGrey3; }
			&--has-value {
				padding-left: 0;
				& + :global(.rs__indicators) {
					display: none; } } }
		&__single-value,
		&__option {
			font-size: 14px;
			img {
				display: inline-block;
				vertical-align: middle;
				width: 30px;
				height: 30px;
				margin-right: 10px;
				text-decoration: none; } }
		&__single-value {
			text-decoration: underline; }
		&__option {
			padding: 6px 12px; }
		&__menu {
			z-index: 2; } } }

