.paid {
	&__label {
		font-size: 14px;
		margin-bottom: 0; } }


.sign {
	&__row {
		margin-top: 17px; }
	&__place {
		position: relative;
		width: 220px;
		height: 90px; }
	&__modal {
		position: relative;
		width: 100%;
		height: 100%;
		overflow: hidden;
		padding: 10px;
		background-color: $lightGrey5;
		border: 1px solid $lightGrey3;
		border-radius: 5px;
		cursor: not-allowed; }
	&__btn {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		cursor: pointer;
		background: transparent;
		border: none;
		z-index: 1;
		padding: 0;
		cursor: pointer;
		&:focus {
			outline: 0; } }
	&__btn-skelet {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		cursor: not-allowed;
		opacity: 0.8; }
	&__create {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $black;
		color: white;
		padding: 0 40px;
		& :global(.fico-contract) {
			font-size: 36px;
			color: white;
			margin-right: 10px; } }
	&__create-text {
		display: inline-block;
		text-align: left;
		font-size: 14px;
		font-weight: bold; }
	&__content {
		@include rh(632) {
			align-self: flex-start; } } }

 :global(.is-keyless) {
	.sign {
		&__create {
			background-color: $keyless; } } }

 :global(.is-standard) {
	.sign {
		&__create {
			background-color: $standard; } } }
