@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

 :global(.column) {
	&.column {
		@include r(767) {
			max-width: unset;
			flex-basis: 100%;
			margin-left: 0; } }
	.label-title:global(.label-title) {
		font-size: 14px;
		margin-bottom: 0; } }

.row:global(.column__row) {
	&:not(:first-child) {
		margin-top: 16px; } }
