@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.container {
	align-items: center;
	display: flex; }

.date {
	background-color: $lightGrey3;
	border-radius: 5px;
	flex-shrink: 0;
	margin-right: 7px;
	overflow: hidden;
	text-align: center;
	width: 33px; }

.month {
	color: black;
	font-size: 9px;
	font-weight: bold;
	line-height: 1;
	padding-top: 2px; }

.day {
	color: black;
	font-size: 13px;
	font-weight: bold;
	line-height: 1; }

.year {
	background-color: $lightGrey2;
	color: $grey;
	font-size: 10px;
	font-weight: 500;
	line-height: 1;
	padding: 2px 0; }

.location {
	font-size: 11px;
	text-decoration: underline;
	cursor: pointer; }

.time {
	font-size: 12px;
	font-weight: 500;
	line-height: 1;
	margin-top: 5px; }

 :global(.card) {
	.location,
	.time {
		font-weight: bold; } }
