@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.pagination {
	& :global(.pagination) {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		margin-top: 50px;
		li {
			&:not(:last-child) {
				margin-right: 5px; }
			&:global(.previous),
			&:global(.next) {
				a {
					width: auto;
					padding: 0 5px; } }
			&:global(.active) {
				a {
					background-color: $black;
					color: white; } }
			&:global(.disabled) {
				display: none; } }
		a {
			display: block;
			width: 30px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			font-size: 13px;
			color: black;
			border: none;
			border-radius: 3px;
			cursor: pointer;
			background-color: transparent;
			text-decoration: none;
			&:hover {
				background-color: $black;
				color: white; }
			&:focus {
				outline: 0; } }
		@include r(767) {
			margin-top: 35px; } } }
