@import "../_variables.sass";

.overlay {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: auto; }

.ReactModal__Body--open {
	overflow: hidden; }

.ReactModal__Overlay {
	overflow: auto;
	padding: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 5; }

.rs-modal {
	width: 100%;
	max-width: 500px;
	background-color: white;
	border: 1px solid $lightGrey3;
	border-radius: 5px;

	&:focus {
		outline: 0; }

	&--lg {
		max-width: 800px; }

	&__header,
	&__body,
	&__footer {
		padding: 15px; }

	&__header {
		border-bottom: 1px solid $lightGrey3;
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }

	&__footer {
		border-top: 1px solid $lightGrey3; }

	&__title {
		font-size: 20px; }

	&__close {
		font-family: inherit;
		font-size: 21px;
		font-weight: 700;
		line-height: 1;
		color: #000;
		text-shadow: 0 1px 0 #fff;
		opacity: .5;
		background-color: transparent;
		border: 0;
		padding: 15px;
		margin: -15px -15px -15px auto;
		&:not(:disabled):not(.disabled) {
			cursor: pointer;
			&:hover {
				opacity: .75; } } }

	&__controls {
		display: flex;
		justify-content: flex-end;
		> :not(:first-child) {
			margin-left: 15px; } } }
