@import "./_mixins.sass";
@import "./modules/_reset.sass";
@import "./_variables.sass";
@import "./modules/modal.sass";

*,
*::before,
*::after {
	box-sizing: border-box; }

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: "Poppins", sans-serif;
	position: relative;
	letter-spacing: normal; }

.container {
	margin: 0 auto;
	max-width: 844px;
	padding: 0 15px;
	width: 100%; }

.jc-fs {
	justify-content: flex-start; }

.jc-c {
	justify-content: center; }

.jc-fe {
	justify-content: flex-end; }

.jc-sb {
	justify-content: space-between; }

.jc-sa {
	justify-content: space-around; }

.ai-fs {
	align-items: flex-start; }

.ai-c {
	align-items: center; }

.ai-fe {
	align-items: flex-end; }

.ai-b {
	align-items: baseline; }

.fw-w {
	flex-wrap: wrap; }

.page-content {
	margin-bottom: 70px; }

@include r(767) {
	.mob-full {
		width: 100%; } }

.react-datepicker__input-container {
	width: auto; }

.react-datepicker-popper {
	user-select: none;
	z-index: 2; }

.react-datepicker {
	@include r(359) {
		min-width: 328px;
		margin-left: -31px; } }

.pac-container {
	margin-top: -70px; }

.pac-item {
	cursor: pointer; }

.pac-logo::after {
	display: none; }

@import "./modules/reservation.sass";
