@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.dc {
	 :global(.column--last) {
		@include r(575) {
			max-width: unset;
			flex-basis: 100%;
			flex-direction: row;
			align-items: baseline;
			text-align: left;
			margin-left: unset;
			justify-content: space-between;
			margin-top: 5px;
			 :global(.label-title) {
				margin-bottom: unset; } } } }


.driver {
	width: 100%;
	&:not(:first-child) {
		margin-top: 10px; }
	&__name {
		text-transform: uppercase; }
	&__docs {
		display: flex;
		flex-wrap: wrap;
		margin-top: 10px; }
	&__doc {
		border: 1px solid $lightGrey4;
		background-color: $lightGrey5;
		border-radius: 3px;
		padding: 2px 8px;
		white-space: nowrap;
		margin-bottom: 10px;
		margin-right: 15px; }
	&__doc-label {
		font-size: 10px; }
	&__doc-num {
		font-size: 14px;
		font-weight: bold; } }

.column--drivers {
	&:global(.column) {
		@include r(767) {
			flex-basis: 66.666666%; }
		@include r(575) {
			flex-basis: 100%; } } }
