@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.message {
	max-width: 100%;
	min-width: 100%;
	border-radius: 3px;
	border: 1px solid $lightGrey3;
	background-color: $lightGrey5;
	font-size: 14px;
	font-family: inherit;
	padding: 15px;
	&:focus {
		outline: 0; } }

.annual {
	&__modal {
		@include rh(312) {
			align-self: flex-start; } }
	&__title {
		color: $rejected; }
	&__text {
		margin-top: 3px; }
	&__error {
		font-size: 12px;
		color: $grey;
		padding: 0 5px;
		margin-top: 5px; } }

.text-column {
	@include rm(768) {
		max-width: 66.666666%; } }
