@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.list,
.reservation {
	width: 100%; }

.label {
	font-size: 13px; }

.reservation {
	position: relative;
	@include r(767) {
		max-width: 360px;
		margin: 0 auto; }
	&:not(:first-child) {
		margin-top: 32px; }

	&__header {
		align-items: baseline;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-bottom: 10px;
		width: 100%;
		.label {
			flex-shrink: 0;
			margin-bottom: 5px;
			&:not(:last-child) {
				margin-right: 35px; } }
		.number {
			font-size: 23px;
			font-weight: bold; }
		.status {
			display: flex;
			align-items: flex-start;
			flex-wrap: wrap; }
		.status-val,
		.type-val {
			font-size: 14px;
			font-weight: bold;
			user-select: none;
			cursor: pointer; } }

	&__body {
		border: 1px solid $lightGrey;
		display: flex;
		flex-wrap: wrap;
		position: relative;
		border-radius: inherit;
		.side {
			width: 100%;
			@include rm(768) {
				width: 50%;
				max-width: unset;
				&:not(:last-child) {
					border-right: 1px solid $lightGrey; } } }
		.label {
			font-size: 12px; } }

	&__footer {
		align-items: flex-start;
		display: flex;
		justify-content: space-between;
		width: 100%;
		@include r(767) {
			flex-wrap: wrap; }
		.label {
			margin-top: 5px;
			margin-right: 15px; }
		.rental-time {
			display: flex;
			align-items: baseline;
			margin-top: 10px;
			flex-wrap: wrap;
			 :global(.label) {
				font-size: 14px;
				margin-right: 10px;
				@include r(767) {
					font-size: 13px; } } } }

	&__bottom-info {
		@include rm(768) {
			padding-right: 15px; }
		@include r(767) {
			width: 100%; } }

	&__todo {
		margin-top: 10px;
		@include rm(768) {
			display: flex;
			align-items: flex-start; }
		 :global(.todo__label),
		 :global(.todo__actions) {
			font-size: 14px; }
		 :global(.todo__label) {
			white-space: nowrap;
			margin-right: 10px;
			@include r(767) {
				font-size: 13px; } } }

	&__btns {
		display: flex;
		flex-wrap: wrap;
		margin-left: auto;
		flex-shrink: 0;
		& > * {
			margin-top: 10px;
			@include r(575) {
				margin-top: 10px; }
			&:not(:last-child) {
				margin-right: 10px; } } }

	&__loading {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		opacity: 0.5; }

	&:global(.is-keyless) {
		.status-val,
		.type-val,
		 :global(.time-left:not(.time-left--red)) {
			color: $keyless; } }
	&:global(.is-standard) {
		.status-val,
		.type-val,
		 :global(.time-left:not(.time-left--red)) {
			color: $standard; } }
	&:global(.is-rejected) {
		.status-val {
			color: $rejected; }
		.reservation {
			&__body {
				border-color: $rejected;
				.side {
					@include rm(768) {
						&:not(:last-child) {
							border-color: $rejected; } } } } } } }

.vehicle {
	align-items: flex-start;
	display: flex;
	padding: 15px 15px 0;
	&__info {
		align-self: center;
		&:not(:last-child) {
			margin-right: 15px; } }
	&__img {
		width: 130px;
		height: 90px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__inner {
		display: inline-block; }
	&__group {
		font-size: 13px;
		margin-top: 10px; }
	&__title {
		font-size: 23px;
		font-weight: bold;
		margin-top: 5px;
		@include r(767) {
			font-size: 18px; } }
	&__similar {
		font-size: 11px;
		margin-top: 2px; }
	@include rm(768) {
		padding: 15px 20px 10px;
		justify-content: space-between; } }

.details {
	.label {
		font-size: 14px;
		font-weight: bold;
		margin-bottom: 10px;
		&:not(:first-child) {
			margin-top: 8px; } }
	&__group {
		align-items: flex-start;
		display: flex;
		margin-top: 8px;
		.label {
			margin-top: 0; } }
	&__group-item {
		&:not(:last-child) {
			margin-right: 35px; } }
	@include rm(768) {
		padding: 25px 20px 10px;
		align-items: flex-start;
		display: flex;
		justify-content: space-between;
		&__column {
			&:nth-child(1) {
				flex-shrink: 0;
				margin-right: 25px;
				max-width: 135px; } }
		.mob-flex {
			&:not(:first-child) {
				margin-top: 8px; } } }
	@include r(767) {
		padding: 15px 15px 10px;
		&__column {
			&--mob {
				display: flex; }
			&:not(:first-child) {
				margin-top: 15px; } }
		.mob-flex {
			&:not(:last-child) {
				margin-right: 15px; } } } }

.extras {
	font-size: 14px; }

.price {
	font-size: 18px;
	font-weight: bold;
	white-space: nowrap; }
