@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.vc {
	 :global(.column--last) {
		@include r(767) {
			align-items: flex-start;
			text-align: left;
			margin-left: unset; }
		@include r(575) {
			margin-top: 15px; } } }

.reserve-num {
	font-size: 24px;
	font-weight: bold;
	line-height: 1; }

.reserve-type,
.reserve-status {
	font-size: 18px;
	font-weight: bold;
	line-height: 24px; }

.reserve-info {
	&:not(:last-child) {
		margin-right: 30px; }
	&--status {
		margin-left: auto;
		text-align: right;
		.reserve-status {
			font-size: 24px; } }
	@include r(767) {
		&:global(.mob-full) {
			margin-bottom: 15px;
			margin-right: 0;
			 :global(.label) {
				display: inline-block; }
			.reserve-num {
				display: inline-block;
				margin-left: 10px; } }
		&:not(:global(.mob-full)) {
			margin-top: 5px; } } }

 :global(.is-keyless) {
	.reserve-type,
	.reserve-status {
		color: $keyless; } }
 :global(.is-standard) {
	.reserve-type,
	.reserve-status {
		color: $standard; } }
 :global(.is-rejected) {
	.reserve-status {
		color: $rejected; } }

.dl-cotainer {
	@include rm(576) {
		padding-right: 10px;
		@include r(767) {
			width: 250px; } }
	@include r(575) {
		display: flex;
		width: 100%;
		align-items: flex-start; } }

.dl-item {
	&:not(:first-child) {
		@include rm(576) {
			margin-top: 10px; }
		@include r(575) {
			margin-left: 15px; } } }

.actions {
	margin-top: 25px; }

.map-place {
	position: relative;
	width: 220px;
	height: 124px;
	&--mt {
		margin-top: 15px; } }

.vehicle {
	padding-top: 0;
	padding-right: 10px;
	@include r(767) {
		&:global(.column) {
			flex-basis: 100%;
			padding-right: 0;
			@include rm(576) {
				align-items: flex-start;
				flex-direction: row;
				> :not(:first-child) {
					margin-left: 10px; } } } }
	&__img {
		width: 180px;
		height: 120px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__group {
		font-size: 14px;
		margin-top: 20px; }
	&__title {
		display: block;
		font-size: 23px;
		font-weight: bold;
		margin-top: 5px;
		margin-bottom: 10px; }
	&__similar {
		display: inline-block;
		font-weight: normal;
		font-size: 12px; }
	&__info {
		@include rm(576) {
			max-width: 240px; } } }


.ld-edit {
	margin-top: 20px;
	@include r(575) {
		margin-top: 5px; }
	.ld-edit__row {
		&:not(:first-child) {
			margin-top: 20px; }
		& :global(.column__row) {
			margin-top: 15px; } }
	.map-row:global(.column__row) {
		@include rm(576) {
			margin-top: 25px; }
		@include r(575) {
			margin-top: 15px; }
		 :global(.column) {
			&:first-child {
				@include r(767) {
					width: 240px;
					flex-basis: auto; }
				@include r(575) {
					width: 100%;
					padding-right: 0; } } } }
	.map-place {
		margin-top: 0;
		@include r(575) {
			margin-top: 15px; } }
	 :global(.column) {
		@include rm(768) {
			flex-basis: 40%; }
		&:first-child {
			padding-right: 10px; }
		 :global(.label) {
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__dates {
		flex: 0 1 40%;
		@include r(767) {
			flex-basis: 240px; } } }
