@import "../../styles/_mixins.sass";
@import "../../styles/_variables.sass";

.container {
	flex-shrink: 0; }

.label {
	font-size: 12px;
	margin-bottom: 8px; }

.actions {
	font-weight: bold; }

.action {
	@include rm(768) {
		display: inline-block;
		&:not(:last-child) {
			&::after {
				content: ", ";
				white-space: pre; } } }
	@include r(767) {
		display: block;
		&::before {
			content: "\2022\00a0"; } } }

 :global(.is-keyless) {
	.actions {
		color: $keyless; } }
 :global(.is-standard) {
	.actions {
		color: $standard; } }
