@import "../../styles/_variables.sass";

.overlay {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 10;
	background: rgba(255, 255, 255, 0.85);
	display: flex;
	align-items: center;
	justify-content: center; }

@keyframes ldsEllipsis1 {
	0% {
		transform: scale(0); }
	100% {
		transform: scale(1); } }

@keyframes ldsEllipsis3 {
	0% {
		transform: scale(1); }
	100% {
		transform: scale(0); } }

@keyframes ldsEllipsis2 {
	0% {
		transform: translate(0, 0); }
	100% {
		transform: translate(19px, 0); } }

.loader {
	display: inline-block;
	position: relative;
	width: 64px;
	height: 64px; }

.circle {
	position: absolute;
	top: 27px;
	width: 11px;
	height: 11px;
	border-radius: 50%;
	background: $black;

	&:nth-child(1) {
		left: 6px;
		animation: ldsEllipsis1 0.6s infinite; }

	&:nth-child(2) {
		left: 6px;
		animation: ldsEllipsis2 0.6s infinite; }

	&:nth-child(3) {
		left: 26px;
		animation: ldsEllipsis2 0.6s infinite; }

	&:nth-child(4) {
		left: 45px;
		animation: ldsEllipsis3 0.6s infinite; } }
