.card {
	position: relative;
	padding: 20px 30px 25px;
	display: flex;
	font-size: 14px;
	@include r(767) {
		flex-wrap: wrap; }
	@include r(575) {
		padding-left: 15px;
		padding-right: 15px; }
	&.ai-end {
		justify-content: flex-end; }
	&:not(:last-child) {
		border-bottom: 1px solid $lightGrey; }
	&__title {
		display: inline-block;
		font-size: 18px;
		line-height: 20px;
		font-weight: bold;
		margin-right: 15px; }
	&__actions {
		display: flex;
		& > :not(:last-child) {
			margin-right: 10px; } } }

.column {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	flex: 0 1 33.333333%;
	&--2 {
		flex-basis: 66.666666%;
		@include r(767) {
			flex-basis: 100%; } }
	&--3 {
		flex-basis: 100%; }
	&--last {
		max-width: 220px;
		align-items: flex-end;
		text-align: right;
		margin-left: auto; }
	&--title {
		@include r(767) {
			flex-basis: 100%;
			padding-bottom: 15px; } }
	&__head {
		padding-top: 10px; }
	&__body {
		padding-top: 10px; }
	.label {
		font-size: 13px;
		margin-bottom: 10px;
		&:not(:first-child) {
			margin-top: 10px; }
		&__title {
			font-size: 12px;
			font-weight: bold;
			margin-bottom: 7px; } }
	.label-title {
		font-size: 12px;
		font-weight: bold;
		margin-bottom: 7px; }
	&__row {
		width: 100%;
		display: flex;
		&:not(:first-child) {
			margin-top: 20px;
			@include r(767) {
				margin-top: 15px; }
			@include r(575) {
				margin-top: 5px; } }
		@include r(767) {
			flex-wrap: wrap; } }

	.price {
		font-size: 18px;
		white-space: nowrap;
		line-height: 1;
		&.bold {
			font-weight: bold; } }

	ul.links-list {
		li {
			&:not(:first-child) {
				margin-top: 13px; } }
		a {
			font: inherit;
			font-size: 12px;
			color: inherit; } } }
