@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.card {
	 :global(.label-title) {
		&:not(:first-child) {
			margin-top: 20px; } } }

.item {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	&:not(:first-child) {
		margin-top: 10px; } }

.data {
	flex-basis: 50%;
	font-size: 14px;
	line-height: 18px;
	padding-right: 10px;
	@include r(575) {
		flex-basis: auto;
		flex-grow: 1; } }

.hidden-input {
	position: absolute;
	z-index: -1;
	opacity: 0;
	&:checked + .label {
		&::after {
			display: block; } } }


.label {
	position: relative;
	display: inline-block;
	font-size: inherit;
	&--radio,
	&--check {
		padding-left: 25px;
		user-select: none;
		cursor: pointer;
		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 9px; }
		&::before {
			left: 0;
			width: 14px;
			height: 14px;
			background-color: rgba(255, 255, 255, 0);
			border: 1px solid $lightGrey3; }
		&::after {
			left: 3px;
			display: none; } }
	&--radio {
		&::before,
		&::after {
			border-radius: 50%;
			transform: translateY(-50%); }
		&::after {
			width: 8px;
			height: 8px;
			background-color: $black; } }
	&--check {
		&::before {
			transform: translateY(-50%);
			border-radius: 3px; }
		&::after {
			width: 13px;
			height: 9px;
			border-left: 2px solid $black;
			border-bottom: 2px solid $black;
			transform: translateY(-97%) rotate(-45deg); } } }

.quantity {
	display: flex;
	height: 18px;
	margin-top: 5px;
	font-size: 12px;
	&__input {
		font-family: inherit;
		font-size: inherit;
		border-width: 1px 0 1px 0;
		border-style: solid;
		border-color: $lightGrey3;
		height: 100%;
		text-align: center;
		width: 25px;
		&:focus {
			outline: none; } }
	&__button {
		flex-shrink: 0;
		width: 18px;
		height: 100%;
		background-color: lightGrey3;
		border: none;
		color: white;
		text-align: center;
		padding: 0;
		cursor: pointer;
		font-family: inherit;
		font-size: inherit;
		position: relative;
		&:focus {
			outline: none; }
		&::after,
		&::before {
			position: absolute;
			top: 50%;
			left: 50%; }
		&.left {
			border-radius: 50% 0 0 50%;
			&::after {
				content: "-";
				margin-top: -9px;
				margin-left: -3px; } }
		&.right {
			border-radius: 0 50% 50% 0;
			&::after {
				content: "+";
				margin-top: -9px;
				margin-left: -4px; } }
		&:hover {
			background-color: $black; } } }

 :global(.is-keyless) {
	.label {
		&--radio {
			&::after {
				background-color: $keyless; } }
		&--check {
			&::after {
				border-color: $keyless; } } }
	.quantity {
		&__button {
			&:hover {
				background-color: $keyless; } } } }
 :global(.is-standard) {
	.label {
		&--radio {
			&::after {
				background-color: $standard; } }
		&--check {
			&::after {
				border-color: $standard; } } }
	.quantity {
		&__button {
			&:hover {
				background-color: $standard; } } } }
