@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.select {
	& :global(.rs) {
		&__control {
			border-radius: 5px;
			width: 183px;
			height: 38px;
			min-height: unset;
			cursor: pointer;
			border: 1px solid $lightGrey3;
			&--is-focused {
				box-shadow: none; } }

		&__single-value {
			font-size: 14px;
			line-height: 18px; }

		&__placeholder {
			font-size: 14px; }

		&__menu {
			margin: 1px 0; }

		&__option {
			font-size: 14px;
			cursor: pointer;
			&--is-focused {
				background-color: $blue30; }
			&--is-selected,
			&:active {
				background-color: $blue; } }

		&__indicator-separator {
			display: none; } } }
