@import "../../../styles/_mixins.sass";
@import "../../../styles/_variables.sass";

.button {
	position: relative;
	background-color: $black;
	border: none;
	color: white;
	font-family: inherit;
	line-height: 1;
	text-decoration: none;
	text-align: center;
	border-radius: 5px;
	font-size: 17px;
	min-width: 130px;
	padding: 10px 20px;
	&:not(:disabled):hover {
		cursor: pointer; }
	&:focus {
		outline: 0; }
	&:disabled, {
		opacity: 0.5;
		cursor: not-allowed; }
	&.is-disabled {
		opacity: 0.5;
		pointer-events: none; }
	&.green {
		background-color: $green; }
	&.blue {
		background-color: $blue; }
	@include r(479) {
		min-width: unset; }
	&.small {
		border-radius: 3px;
		font-size: 12px;
		min-width: 75px;
		padding: 4px 10px; }
	&__loader {
		width: 100%;
		height: 100%;
		opacity: 0.5;
		position: absolute;
		left: 0;
		top: 0;
		cursor: not-allowed; } }
